
// --------------------------------------------------
//  back and front end contracts / endpoints HERE
// --------------------------------------------------

export function Endpoints() {
    return {
        authenticate: 'authenticate_v1',
        socialID: 'socialID_v1',
        onboarding: 'onboarding_v1',
        checkToken: 'checkToken_v1',
        resendToken: 'resendToken_v1',
        passwordRecovery: 'passwordRecovery_v1',
    }
}

export function authenticate_Input(email, password, _language) {
    return {
        email: email,
        password: password,
        _language: _language,
    }
}

export function authenticate_Output(payload) {
    return {
        token: payload.token,
        user: {
            email: payload.user.email,
            login: payload.user.login,
            hsh: payload.user.hsh,
        }
    }
}

export function socialID_Input(sID,_language) {
    return {
        sID: sID,
        _language: _language,
    }
}

export function resendToken_Input(sID,_language) {
    return {
        sID: sID,
        _language: _language,
    }
}

export function onboarding_Input(sID,sName,sEmail,sPass,_language) {
    return {
        sID: sID,
        sName: sName,
        sEmail: sEmail,
        sPass: sPass,
        _language: _language,
    }
}

export function checkToken_Input(sID,sToken,_language) {
    return {
        sID: sID,
        sToken: sToken,
        _language: _language,
    }
}

export function passwordRecovery_Input(sEmail,_language) {
    return {
        sEmail: sEmail,
        _language: _language,
    }
}
