
export class MenuAppLanguage {

    constructor() {
        this.availableLanguages = [
            'English',
            'Español',
            'Português BR',
            'Português PT']

        this.langs = [
            // english (0)
            {
                labels: [
                    'Dashboard', /* 0 */
                    'User', /* 1 */
                    'Login', /* 2 */
                    'Register', /* 3 */                    
                ]
            },
            // spanish (1)
            {
                labels: [
                    'Panel de control', /* 0 */
                    'Usuario', /* 1 */
                    'Iniciar sesión', /* 2 */
                    'Registrarse', /* 3 */
                ]
            },
            // ptBr (2)
            {
                labels: [
                    'Dashboard', /* 0 */
                    'Usuário', /* 1 */
                    'Login', /* 2 */
                    'Registrar', /* 3 */
                ]
            },
            // ptPt (3)
            {
                labels: [
                    'Dashboard', /* 0 */
                    'Usuário', /* 1 */
                    'Login', /* 2 */
                    'Registrar', /* 3 */
                ]
            },
        ];
    }
}

export function Menu_MultiLanguage(index) {
    
    var curLanguage = localStorage.getItem('appLanguage');
    if (curLanguage == undefined) {
        curLanguage = 2;
        // ptBR default
        localStorage.setItem('appLanguage', curLanguage)
    }
    curLanguage = parseInt(curLanguage)

    var appLang = new MenuAppLanguage();

    return appLang.langs[curLanguage].labels[index];
}

export function BuildMenuTranslation()
{
    return {
        dashboardItem: Menu_MultiLanguage(0),
		userItem: Menu_MultiLanguage(1),
		loginItem:  Menu_MultiLanguage(2),
		registerItem: Menu_MultiLanguage(3),
    };
}

export default class {

	static getHtml() {
        var itens = BuildMenuTranslation();
		return `<div class="nav-menu" align='left'><nav class="menu">
                <div class="nav-header" style='height:150px'>
                    <a href='/' style='cursor:pointer'>
                    <img src="src/static/img/menuLogo.png" alt='Dashboard' style="width:300px;height:105px;padding-top:15px"></a>
                </div>
				<div class="nav-container">
					<ul class="main-menu">
						<li class="active"><a href='/' style='cursor:pointer'>${itens.dashboardItem}</a></li>
						<li>
							<a href="javascript:void(0);">${itens.userItem}</a>
							<ul>
								<li><a href="/login" style='cursor:pointer'>${itens.loginItem}</a></li>
								<li><a href="/register" >${itens.registerItem}</a></li>									
							</ul>
						</li>						
					</ul>
				</div>
			</nav>
		</div>`;
	}
}
